import React from 'react';
import {CardBody,CardText} from 'reactstrap';
export default function Card(note){
        return
                 note.map(note=>{ return( <div className="card border-success mb-3" style={{maxWidth: "20rem"}}>
        
                 <div className="card-body">
                  <h4 className="card-text">{note}</h4>
                   
                 </div>
          </div>)})

                
               

    
}